*{
  padding: 0;
  margin: 0;
  font-family: 'Jameel Noori Nastaleeq';
  direction: rtl;
}



.App {
  text-align: center;
  
}

.title h1{
    color:#FFC300 ;
    font-size: 2.5rem;
    text-shadow: 0 .3rem 1rem rgba(0,0,0,.5);
  }

  ul{
    display: flex;
    list-style: none;
      height:3.5rem;
      /* background:#155768; */
      color:#fff;
      border:none;
      box-shadow: 0 .3rem 1rem rgba(0,0,0,.3);
      font-size: 2.5rem;
      direction: ltr;
      
    
    }


    li{
      margin:-.7rem 1rem;
    }

    a{
      color: #ffffff;
      font-size: 1.5rem;
      transition: .2s;
      text-decoration: none;
    }

    a:hover{
      color:#FFC300 ;
    
    }


    .heading h1{
      margin: 1rem;
      color:#c4c4c4 ;
      font-size: 2.5rem;
      text-shadow: 0 .3rem 1rem rgba(0,0,0,.5);
    }
    div button .main-btn{
      height:4rem;
      width:20rem;
      padding: 0;
      background:#070707;
      color:rgb(20, 19, 19);
      border:none;
      border-radius: .8rem;
      box-shadow: 0 .3rem 1rem rgba(0,0,0,.3);
      cursor: pointer;
      font-size: 1.2rem;
      transition: .2s;
      margin:.8rem .5rem;
      

    }
    button{
      height:4rem;
      width:16.5rem;
      padding: 0;
      background:#155768;
      color:#fff;
      border:none;
      border-radius: .8rem;
      box-shadow: 0 .3rem 1rem rgba(0,0,0,.3);
      cursor: pointer;
      font-size: 1.2rem;
      transition: .2s;
      margin:.8rem .5rem;
      
    }

    button:hover{
      background-color:#FFC300 ;
      color:#282c34 ;
    
    }

    span{
      margin: 4rem;
    }


    .makhraj{
      display: flex;
      align-items: center;
      justify-content: center;
    }
    .makhraj h1{
      flex-basis: 27%;
      margin: .7rem;
      height: 3.5rem;
      border-radius: 3rem;
      box-shadow: 0 .3rem .8rem rgba(33, 252, 44, 0.9);
      padding-top: .5rem;
        
      font-size: 2rem;
        color:#fff;
        border:none;
    }


    .makhraj h2{
      flex-basis: 20%;
      /* column-gap: 5rem; */
      border-radius: .5rem;
      box-shadow: 0 .3rem .8rem rgba(0, 255, 13, 0.5);
        font-size: 1.7rem;
        color:#FFC300 ;
        border:none;
        padding: .5rem .1rem;
        margin:.5rem 1rem 1rem;
    }
    
    .makhraj span{
      margin: 1rem;
    }

    input{
      flex-basis: 25%;
      margin: .7rem;
      height: 3.5rem;
      border-radius: 3rem;
      background: none;
      box-shadow: 0 .3rem .8rem rgba(33, 252, 44, 0.9);
        font-size: 1.5rem;
        color:#fff;
        border:none;
        text-align: center;
        direction: ltr;
    }



  

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  /* display: flex; */
  /* flex-direction: column; */
  align-items: center;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
